.site-list {
    list-style: none;
}

.site-list__items {
    text-align: center;
}

.site-list__items a {
    font-size: 15px;
    line-height: 32px;
    color: #cdcdcd;
    text-decoration: none;
}

.site-list__items a:hover {
    color: white;
    border-bottom: 2px solid #f1cdb3;
}

.main-section {
    background-color: #3c322d;
    border-top: 1px solid #3c322d;
}

.main-section__wrapper {
    padding: 0 10px;
    padding-bottom: 35px;
}

.main-section h2 {
    font-family: "Geogia", "Arial", sans-serif;
    font-size: 32px;
    line-height: 32px;
    color: white;
    margin-bottom: 30px;
    margin-top: 30px;
    text-align: center;
}

.main-section p {
    font-size: 15px;
    line-height: 24px;
    color: #cdcdcd;
    text-align: center;
    margin-bottom: 30px;
}

.main-section__link {
    margin-bottom: 10px;
    margin: 0 auto;
}

.button {
    display: block;
    font-family: "Georgia", "Arial", sans-serif;
    text-decoration: none;
    color: black;
    background-color: #f1cdb3;
    border-radius: 30px;
    font-size: 17px;
    line-height: 50px;
    width: 205px;
    height: 50px;
    text-align: center;
}

.button:hover {
    background-color: #fddcc4;
}

.about-us {
    padding: 0 10px;
    margin-bottom: 30px;
}

.about-us__item p {
    font-size: 15px;
    line-height: 24px;
    margin-bottom: 30px;
}

.about-us__item p:last-child {
    margin-bottom: 0;
}

p.about-us__title,
.help-shelter__title,
.animals__title {
    font-family: "Georgia", "Arial", sans-serif;
    font-size: 30px;
    line-height: 40px;
    margin-top: 30px;
}

.animals {
//    height: 835px;
    background-color: #f6f6f6;
    border-top: 1px solid #f6f6f6;
    border-bottom: 1px solid #f6f6f6; 
}
.animals__wrapper {
    padding: 0 10px;
}
.animals__title {
    margin-top: 60px;
    margin-bottom: 45px;
    text-align: center;
}
.animals__main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 40px;
}
.animals__item {
    width: 270px;
    height: 424px;
    text-align: center;
    margin: 0 auto;
    padding-top: 290px;
    border-radius: 8px;
    margin-bottom: 25px;
}
.animals__item:nth-child(1) {
    background: url("../pictures/animals-cat.png") no-repeat;
    background-position: top;
    background-color: #fafafa;
}

.animals__item:nth-child(2) {
    background: url("../pictures/animals-pet.png") no-repeat;
    background-position: top;
    background-color: #fafafa;
}
.animals__item:nth-child(3) {
    background: url("../pictures/animals-dog.png") no-repeat;
    background-position: top;
    background-color: #fafafa;
    margin-bottom: 0;
}


.animals__item span {
    font-size: 20px;
    line-height: 45px;
}
.animals-link {
	margin: 0 auto;
    width: 188px;
    background-color: transparent;
    border: 2px solid #f1cdb3;
}
.animals-link:hover {
    background-color: #f1cdb3;
}
.animals__link {
    margin: 0 auto;
	margin-bottom: 40px;
}
.help-shelter {
    margin-bottom: 60px;
}
.help-shelter__title {
    text-align: center;
    margin-top: 60px;
    margin-bottom: 60px;
}
.help-shelter__item {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0 10px;
    list-style: none;
}
.help-shelter__items {
    font-family: "Georgia", "Arial", sans-serif;
    font-size: 20px;
    line-height: 45px;
    width: 200px;
    height: 100px;
    padding-top: 60px;
    text-align: center;
}

.help-shelter__items:nth-child(1) {
    background: url("../pictures/pet-food.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.help-shelter__items:nth-child(2) {
    background: url("../pictures/ambulance.png");
    background-repeat: no-repeat;
