    margin-bottom: 20px;
}
.help-shelter__items:nth-child(4) {
    background: url("../pictures/bowl.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.help-shelter__items:nth-child(5) {
    background: url("../pictures/shampoo.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.help-shelter__items:nth-child(6) {
    background: url("../pictures/pills.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.help-shelter__items:nth-child(7) {
    background: url("../pictures/syringe.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.help-shelter__items:nth-child(8) {
    background: url("../pictures/leash.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.help-shelter__items:nth-child(9) {
    background: url("../pictures/cat.png");
    background-repeat: no-repeat;
    background-position: center top;
    margin-bottom: 20px;
}
.donation {
	background-color: #f6f6f6;
    padding: 0 10px;
    padding-top: 40px;
    padding-bottom: 40px;
}
.donation__wrapper {
    width: 320px;
	margin: 0 auto;
}
.donation__item p {
	font-family: "Georgia", "Arial", sans-serif;
    font-size: 25px;
    line-height: 35px;
    display: block;
    margin-bottom: 20px;
}
.donation__item span {
	font-family: "Georgia", "Arial", sans-serif;
    font-size: 15px;
    line-height: 25px;
    display: block;
    margin-bottom: 15px;
}
.donation__item small {
    font-size: 12px;
    line-height: 18px;
    font-style: italic;
    color: #b2b2b2;
}
.donation__item-link {
    font-family: "Times New Roman", "Arial", serif;
    display: block;
    border-radius: 9px;
    font-size: 21px;
    line-height: 52px;
    padding-left: 28px;
    width: 300px;
    position: relative;
    margin-bottom: 25px;
}
.donation__item-link::after {
    content: "";
    background: url("../pictures/credit-card.png") no-repeat;
    width: 30px;
    height: 24px;
    position: absolute;
    top: 13px;
    left: 12px;
}
.page-footer {
	background-color: #4b3d37;
    padding-top: 50px;
    border-bottom: 1px solid #4b3d37;
}
.page-footer__wrapper {
    padding: 0 10px;
}
.page-footer__feedback,
.page-footer__location {
    width: 300px;
    margin: 0 auto;
    margin-bottom: 30px;
}
.page-footer__location {
    margin-bottom: 0;
}
.feedback__text, 
.location__text {
    display: block;
    color: white;
    font-family: "Geprgia", "Arial", sans-serif;
    font-size: 25px;
    line-height: 35px;
    margin-bottom: 25px;
}
.feedback__email,
.feedback__phone,
.location__link {
    display: block;
    padding-left: 50px;
    color: #f1cdb3;
    font-family: "times New Roman", "Arial", sans-serif;
    font-size: 20px;
    line-height: 45px;
    margin-bottom: 25px;
    text-decoration: none;
    position: relative;
}
.location__link:nth-child(3) {
    margin-bottom: 10px;
}
.feedback__phone {
    margin-bottom: 0;
}
.feedback__email::before {
    content: "";
    background: url("../pictures/email.png") no-repeat;
    width: 40px;
    height: 26px;
    position: absolute;
    top: 10px;
    left: 2px;
}
.feedback__email:hover,
.feedback__phone:hover,
.location__link:hover {
    color: #fddcc4;
}
.feedback__phone::before {
    content: "";
    background: url("../pictures/phone.png") no-repeat;
    width: 40px;
    height: 32px;
    position: absolute;
    top: 5px;
    left: 2px;
}
.location__link::before {
    content: "";
    background: url("../pictures/pin.png") no-repeat;
    width: 26px;
    height: 36px;
    position: absolute;
    top: 6px;
    left: 7px;
}
@import "page-main__tablet.scss";
@import "page-main__desktop.scss";
